'use client';

import React from 'react';
import { Button, Flex, Space } from 'antd';
import { 
  UndoOutlined, 
  CloseOutlined, 
  HeartFilled, 
  StarFilled, 
  ThunderboltFilled 
} from '@ant-design/icons';

interface ActionButtonsProps {
  onUndo: () => void;
  onPass: () => void;
  onSuperLike: () => void;
  onLike: () => void;
  onBoost: () => void;
  canUndo: boolean;
  hasCard: boolean;
}

export function ActionButtons({
  onUndo,
  onPass,
  onSuperLike,
  onLike,
  onBoost,
  canUndo,
  hasCard
}: ActionButtonsProps) {
  if (!hasCard) return null;

  const containerStyle: React.CSSProperties = {
    paddingBottom: 32,
    paddingTop: 16,
    paddingLeft: 16,
    paddingRight: 16,
    background: 'linear-gradient(to top, rgba(0,0,0,0.5) 0%, transparent 100%)'
  };

  const buttonGroupStyle: React.CSSProperties = {
    maxWidth: 384,
    margin: '0 auto'
  };

  const baseButtonStyle: React.CSSProperties = {
    borderRadius: '50%',
    boxShadow: '0 4px 16px rgba(0,0,0,0.2)',
    transition: 'all 0.2s ease',
    border: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  const smallButtonStyle: React.CSSProperties = {
    ...baseButtonStyle,
    width: 48,
    height: 48
  };

  const largeButtonStyle: React.CSSProperties = {
    ...baseButtonStyle,
    width: 56,
    height: 56
  };

  return (
    <div style={containerStyle}>
      <Flex justify="center" align="center" style={buttonGroupStyle}>
        <Space size="large">
          {/* Undo Button */}
          <Button
            style={{
              ...smallButtonStyle,
              backgroundColor: '#ffffff',
              opacity: canUndo ? 1 : 0.5,
              cursor: canUndo ? 'pointer' : 'not-allowed'
            }}
            onClick={onUndo}
            disabled={!canUndo}
            icon={<UndoOutlined style={{ color: '#666666', fontSize: 18 }} />}
          />

          {/* Pass Button */}
          <Button
            style={{
              ...largeButtonStyle,
              backgroundColor: '#ffffff'
            }}
            onClick={onPass}
            icon={<CloseOutlined style={{ color: '#ff4d4f', fontSize: 24, fontWeight: 'bold' }} />}
          />

          {/* Super Like Button */}
          <Button
            style={{
              ...smallButtonStyle,
              backgroundColor: '#ffffff'
            }}
            onClick={onSuperLike}
            icon={<StarFilled style={{ color: '#1890ff', fontSize: 18 }} />}
          />

          {/* Like Button */}
          <Button
            style={{
              ...largeButtonStyle,
              backgroundColor: '#52c41a'
            }}
            onClick={onLike}
            icon={<HeartFilled style={{ color: '#ffffff', fontSize: 24 }} />}
          />

          {/* Boost Button */}
          <Button
            style={{
              ...smallButtonStyle,
              backgroundColor: '#ffffff'
            }}
            onClick={onBoost}
            icon={<ThunderboltFilled style={{ color: '#722ed1', fontSize: 18 }} />}
          />
        </Space>
      </Flex>
    </div>
  );
}